import Router from 'vue-router'

const routes = [
    {
        path: '/',
        redirect:'/app',
    },
    {
        path: '/app',
        // path: '/app/:id?',
        component: () => import('../views/appApply/AppApply.vue')
    },
]
function getAbsolutePath () {
  let path = location.pathname
  return path
}
export default () => {
    return new Router({
        routes,
        mode: 'hash',
        base: getAbsolutePath()
    })
}
